.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
marquee {
  font-size: 20px;
  font-weight: 800;
  font-family: "Noto Sans", sans-serif;
  font-family: "Oxygen", sans-serif;
  color: #104e8b;
  /* text-shadow: 2px 2px #afeeee; */
}
.cardCenter {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

footer {
  background-color: #007bff !important;
  padding: 10px;
  text-align: center;
  color: white;
}
.hscroll {
  overflow-x: auto; /* Horizontal */
}
.carousel-inner {
  width: 80%;
  max-height: 550px !important;
  vertical-align: middle;

  /*display: flex; justify-content: center; object-fit: cover; */
}
.carousel-item {
  height: 550px;
  /*width: 50%; */
  margin: auto;
  /* width: auto !important; */
  /* margin: auto; */
}

.table-responsive {
  /* max-height:300px; */
}
.bg-primary_change {
  background-color: #563d7c !important;
}

.navbar {
  min-height: 4rem;
}

.navbar-nav > li > a {
  padding-top: 0px;
  padding-bottom: 0px;
  line-height: 80px;
}

@media (max-width: 767px) {
  .navbar-nav > li > a {
    line-height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.navbar-brand:hover {
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.5);
}
